
const camelCase: Function = ( input: string ): string => {
    return input.toLowerCase().replace( /-(.)/g, function( match: string, group: string ): string {
        return group.toUpperCase();
    } );
};

const getAvaliableBreakpoints: Function = (): Object => JSON.parse( window.getComputedStyle( body, ':before' )
    .getPropertyValue( 'content' ).replace( /"/g, '' ).replace( /\\/g, '"' ) );

const getCurrentBreakpoint: Function = (): number => +window.getComputedStyle( body, ':after' )
    .getPropertyValue( 'content' ).replace( /"/g, '' );

const body: HTMLElement = document.querySelector( 'body' );
const breakpoint: any = {
    current: getCurrentBreakpoint(),
};

const breakpoints: Object = getAvaliableBreakpoints();
Object.keys( breakpoints ).forEach( ( breakpointName: string ): void => {
    breakpoint[ camelCase( breakpointName ) ] = breakpoints[ breakpointName ];
});

let passiveOption: any = undefined;
try {
    const opts: any = Object.defineProperty( {}, 'passive', {
        get: function(): void {
            passiveOption = { passive: true };
        },
    } );
    window.addEventListener( 'test', null, opts );
} catch (e) {}

window.addEventListener( 'resize', () => {
    breakpoint.current = getCurrentBreakpoint();
}, passiveOption );

export default breakpoint;
